angular.module("cn.batch-forms").run(["$templateCache", function($templateCache) {$templateCache.put("cn-batch-forms/batch-results.html","<div class=\"cn-modal\">\n  <div class=\"modal-header cn-heading row vertical-parent\">\n    <cn-flex-form-header\n      ff-header-config=\"vm.headerConfig\"\n      ff-submit=\"vm.done()\">\n    </cn-flex-form-header>\n  </div>\n  <div class=\"modal-body cn-list\"\n       cn-responsive-height=\"80\"\n       cn-responsive-break=\"sm\"\n       cn-set-max-height>\n    <ul class=\"list-group gutterless\">\n      <li ng-repeat=\"result in vm.results\"\n          class=\"list-group-item\"\n          ng-class=\"{\n            \'text-danger\': result.status != 200,\n            \'text-primary\': result.status == 200\n          }\">\n        <div class=\"row\">\n          <div class=\"col-sm-1 text-center\">\n            <i class=\"fa fa-{{result.status == 200 ? \'check\' : \'times\'}}\"></i>\n          </div>\n          <div class=\"col-sm-11\"\n               ng-show=\"result.status == 200\">\n            {{result.body[vm.displayName]}} ({{result.body.id}}):\n            updated successfully\n          </div>\n          <div class=\"col-sm-11\"\n               ng-show=\"result.status != 200\">\n            {{vm.originals[$index][vm.displayName]}} ({{vm.originals[$index].id}}):\n            {{result.body.message}}\n          </div>\n        </div>\n      </li>\n    </ul>\n  </div>\n</div>\n");}]);